@import "../../../_variables.scss";
@import "../../../_font.scss";

.distributor-form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
  background-color: $gray-50;
  font-family: var(--font-family-base), serif;

  &__title {
    font-size: 1.5rem;
    font-weight: 700;
    color: $gray-900;
  }

  &__fields {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 0;
    background-color: #fff;
    padding: 1.5rem;
    border: 1px solid $gray-200;
    border-radius: 12px;
    box-shadow: 0 2px 6px rgba($gray-600, 0.05);
    align-items: center;
  }

  &__label,
  &__input {
    font-size: 0.95rem;
    font-weight: 500;
    color: $gray-900;
    padding: 0.5rem;
    border-left: 1px solid $gray-200;

    &:first-child {
      border-left: none;
    }
  }

  &__input {
    input {
      width: 100%;
      padding: 0.5rem 0.75rem;
      font-size: 1rem;
      border: 1px solid $gray-200;
      border-radius: 6px;
      background-color: $gray-50;
      color: $gray-900;
    }
  }

  &__submit-button {
    align-self: flex-start;
    padding: 0.75rem 1.25rem;
    font-size: 1rem;
    font-weight: 600;
    background-color: $gray-900;
    color: #fff;
    border: none;
    border-radius: 8px;
    cursor: pointer;

    &:hover {
      background-color: darken($gray-900, 5%);
    }

    &:disabled {
      background-color: $gray-400;
      cursor: not-allowed;
    }
  }

  &__footer {
    text-align: right;
    font-size: 0.85rem;
    color: $gray-600;
    padding-top: 1rem;
    border-top: 1px solid $gray-200;
  }
}
